.button {
    gap: 5px;
    font-size: 16px;
    line-height: 1em;
    font-family: "DM Sans", "DM Sans Placeholder", sans-serif;
    font-weight: 400;
    width: max-content;
    display: flex;
    place-items: center;
    text-decoration: none;
    font-style: normal;
    letter-spacing: 0;
    padding: 10px 16px;
    color: rgb(249, 247, 235);
    background-color: rgb(31, 76, 52);
    border-radius: 10px;
    user-select: none;
    white-space: nowrap;
    cursor: pointer;
    transform: none;
}

.button:hover {
    background-color: rgba(31, 76, 52, 0.8);
}

.active {
    animation: button-click 0.5s ease-out forwards;
    box-shadow: 4px 4px rgba(0, 0, 0, 0.5);
}

.inactive {
    animation: button-inactive 0.5s ease-in forwards;
}

@keyframes button-click {
    0% {
        transform: none;
        box-shadow: 0 0 rgba(0, 0, 0, 0.5);
    }
    100% {
        transform: translateY(-2px);
        box-shadow: 4px 4px rgba(0, 0, 0, 0.5);
    }
}

@keyframes button-inactive {
    0% {
        transform: none;
        box-shadow: 4px 4px rgba(0, 0, 0, 0.5);
    }
    100% {
        transform: translateY(2px);
        box-shadow: 0 0 rgba(0, 0, 0, 0.5);
    }
}